import React from "react";

export default function SayHi() {
  return (
    <>
      <div
        id="sayhi"
        className="w-screen py-10 flex flex-col justify-center items-center space-y-4 bg-[url('images/bg1.jpeg')] bg-cover bg-blend-darken"
      >
        <div className="text-5xl">Say Hi</div>
        <div className="flex justify-around items-center space-x-4">
          <span className="w-[90vw] sm:w-[80vw] md:w-[70vw]">
            <form method="get" className="bg-blue-500 p-4 rounded-lg space-y-2">
              <input
                id="name"
                type="text"
                className="outline-none border-2 rounded-lg group-focus:border-blue-500 w-full p-2"
                placeholder="First Name"
                required
              />
              <input
                id="name"
                type="text"
                className="outline-none border-2 rounded-lg group-focus:border-blue-500 w-full p-2"
                placeholder="Last Name"
                required
              />
              <input
                id="name"
                type="text"
                className="outline-none border-2 rounded-lg group-focus:border-blue-500 w-full p-2"
                placeholder="Email"
                required
              />
              <textarea
                name="description"
                id="description"
                cols="30"
                rows="10"
                placeholder="Description"
                className="outline-none border-2 rounded-lg group-focus:border-blue-500 w-full p-2"
                required
              ></textarea>
              <button type="submit" className="bg-orange-400 w-full p-2 rounded-lg text-white">Say Hi!</button>
            </form>
          </span>
        </div>
      </div>
    </>
  );
}
