import React from 'react';

function Navbar() {
  return (
    <>
    <nav className='flex justify-around items-center bg-transparent' id='home'>
        <span className='space-x-8 p-2 text-2xl'>
            <a href="#home" className='border-b-2 border-transparent hover:border-b-black transition-all duration-500 ease-in-out'>Home</a>
            <a href="#projects" className='border-b-2 border-transparent hover:border-b-black transition-all duration-500 ease-in-out'>Projects</a>
            <a href="#profiles" className='border-b-2 border-transparent hover:border-b-black transition-all duration-500 ease-in-out'>Profiles</a>
            <a href="#sayhi" className='border-b-2 border-transparent hover:border-b-black transition-all duration-500 ease-in-out bg-gray-800 text-white p-1 px-2 rounded-lg'>Say Hi!</a>
        </span>
    </nav>
    </> 
  )
}

export default Navbar;